import ButtonDota from "@/components/utils/cards/ButtonDota"
import useAxios from "@/hooks/useAxios"
import { User } from "@/types/User"
import { Link, Modal } from "@nextui-org/react"
import { useState } from "react"
import FormSignal from "./components/formSignal"
import SignalsCarousel from "./components/signalsCarousel"

const Signals = () => {
    const [modal, setModal] = useState(false)
    const { data: user } = useAxios<User>({ url: 'users/me', method: 'get' })
    return (
        <div className='flex flex-col gap-4 w-full'>
            <Modal isOpen={modal} onClose={() => setModal(false)}>
                <FormSignal onClose={() => setModal(false)} />
            </Modal>
            <div className='absolute z-[2] top-24 left-10 sm:top-20 md:top-24 md:left-24 xl:left-[118px] xl:top-24'>
                <img src="/img/gibor-signals.svg" className='w-[180px]' alt="" />
            </div>
            <div className='absolute block h-[350px] w-[93%] md:w-[95%] 2xl:w-[97%]' >
                <div className='bg-no-reapeat object-none h-full bg-[right_45%_top_50%] rounded-3xl' style={{ backgroundImage: "url('/img/banner/signals.webp')" }}></div>

            </div>

            <span className="text-white text-[28px] z-[2] mt-36 ml-6 font-bold leading-16 md:p-2 md:p-0 md:hidden truncate fade-text">
                Últimas señales
            </span>


            <div className={`flex gap-4 md:items-end md:justify-end z-[2] ml-[-4px] mt-0  px-8 py-0  ${user && user.is_trader ? 'md:mt-16' : 'md:mt-24'}`}>

                <div className='px-4 py-2 border-2 border-black font-semibold border-solid rounded-lg bg-transparent lg:hidden text-black text-[16px]'>
                    <Link href={'/'}>Regresar</Link>
                </div>
                {user && user.is_trader && (
                    <ButtonDota classNames=' md:w-auto bg-custom-gradient text-white' onClick={() => setModal(true)}>
                        Agregar Señal
                    </ButtonDota>
                )}
            </div>

            <div className='flex flex-col gap-8 md:gap-20 md:px-8 '>
                <SignalsCarousel />
                {/* <SignalsGrid /> */}
            </div>
        </div>
    )
}

export default Signals