import { FC, useRef, useState } from "react"
import Slider from 'react-slick'
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'
import { IoIosArrowRoundForward } from "react-icons/io";
import { IoIosArrowRoundBack } from "react-icons/io";
import dayjs from "dayjs"
import useAxios from "@/hooks/useAxios";
import Link from "next/link";
import { User } from "@/types/User";
import { Spinner } from "@nextui-org/react";


type SignalProp = {
    par: any
    key: number
}







const SignalsCarousel = () => {
    const sliderSignalsRef = useRef(null)
    const [activeIndex, setActiveIndex] = useState<number>(0)
    const paresHook = [{
        par: 'prueba par',
        name_user: 'Luciano',
        id: '1',
        created_at: new Date()
    }]
    const settings2 = {
        dots: false,
        arrows: false,
        infinite: false,
        speed: 500,
        slidesToShow: 6,
        slidesToScroll: 1,
        centerMode: false,
        beforeChange: (current: number, next: number) => setActiveIndex(next),
        responsive: [
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    dots: false,
                },
            },
            {
                breakpoint: 1040,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    dots: false,
                },
            },
            {
                breakpoint: 1640,
                settings: {
                    slidesToShow: 4,
                    slidesToScroll: 1,
                    dots: false,
                },
            },
        ],
    }

    if (paresHook.length === 0) return (
        <span className="text-black mt-4 text-3xl font-bold leading-18 fade-text">
            Parece que todavía no tienes señales
        </span>
    )

    return (
        <div className="flex flex-col gap-8 p-8 md:px-8 xl:px-14">
            <div className="hidden mt-4 md:mt-0 md:flex z-[2]">
                <div className="flex flex-col items-left ">
                    <span className=" text-[48px] font-bold leading-18 p-2 md:p-0 truncate fade-text">
                        Últimas señales
                    </span>
                </div>
            </div>
            <div className="relative w-full bg-white rounded-3xl">
                <div className="relative ">
                    <Slider ref={sliderSignalsRef} {...settings2} className="gap-4" initialSlide={0}>
                        {paresHook && paresHook.map((par: any, index) => (
                            <CardSignal par={par} key={index} />
                        ))}
                    </Slider>
                    <div
                        className="absolute top-40 lg:top-40 left-6 cursor-pointer flex items-center justify-center text-center  p-1"
                        onClick={() => sliderSignalsRef.current.slickPrev()}
                    >
                        <div className="absolute bg-white opacity-80 rounded-full w-full h-full"></div>
                        <span className="text-[32px] text-[#ab58f5] z-[2]">
                            <IoIosArrowRoundBack />
                        </span>
                    </div>
                    <div
                        className="absolute top-40 lg:top-40 right-6 cursor-pointer flex items-center justify-center  p-1"
                        onClick={() => sliderSignalsRef.current.slickNext()}
                    >
                        <div className="absolute bg-white opacity-80 rounded-full w-full h-full"></div>
                        <span className="text-[32px] text-[#ab58f5] z-[2]">
                            <IoIosArrowRoundForward />
                        </span>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default SignalsCarousel

const CardSignal: FC<SignalProp> = ({ par, key }) => {
    const { data: user } = useAxios<User>({ url: 'users/me', method: 'get' })
    const fecha = (date: any) => {
        if (!date) return
        return dayjs(date.seconds * 1000).format('dddd, D MMM YYYY')
    }
    const removeGuion = (par: string) => {
        if (!par) return
        return par.replace("-", "")
    }
    if (!user) return (<Spinner />)
    return (
        <div key={key} className="rounded-3xl p-4 m-3 mr-4 mb-6 mt-3 bg-[#f2f3f7] border border-gray-300 border-solid flex flex-col gap-4 ">
            <div className="relative flex justify-center">
                <img className="w-[200px]" src={`img/PARES/${par?.par}.svg`} alt="" />
            </div>
            <div className="flex flex-col gap-2 items-start">
                <span className="text-[18px] font-Urbanist font-bold leading-[23.4px] text-black">{removeGuion(par.par)}</span>
                <div className="flex flex-col gap-1 w-full text-left">
                    <span className="text-[10px] font-Urbanist text-black truncate">{par?.name_user} | <span className="text-gray-500">{fecha(par?.created_at)}</span></span>
                    <Link href={par.id} onClick={() => window.location.href = `/signals/${par.id}`} className="hover:bg-custom-gradient hover:border-transparent hover:border-x-0 transision-all  duration-300 hover:text-white bg-white rounded-md px-4 py-2 font-bold text-center border-2 border-solid border-[#ab58f5] text-[#ab58f5]">Ver señal</Link>
                    {user?.id === par.id_user && (<Link href={`${par.id}/update`} className="hover:bg-custom-gradient hover:border-transparent hover:border-x-0 transision-all hover:text-white duration-300 bg-white rounded-md px-4 py-2 font-bold text-center border-2 border-solid border-[#ab58f5] text-[#ab58f5]">Editar señal</Link>)}
                </div>
            </div>
        </div>
    )
}